@charset "UTF-8";
body {
  margin: 0 auto;
  padding: 0; }

.logo {
  display: flex;
  align-self: center;
  margin-top: 18px;
  margin-bottom: 18px;
  padding: 2px 25px 0 25px;
  width: 100px; }

.page-header__wrapper {
  max-width: 1200px;
  min-height: 99px;
  margin: 0 auto;
  display: flex;
  flex-direction: row; }

.main-nav {
  display: flex;
  flex-grow: 1;
  justify-content: flex-end; }

.main-nav__list {
  display: flex; }

.site-list {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  list-style: none; }

.site-list__link {
  display: block;
  padding-top: 35px;
  padding-bottom: 35px;
  padding-left: 45px;
  padding-right: 45px;
  text-decoration: none; }

.info-contacts {
  display: flex; }

.info-contacts__tel {
  display: block;
  padding-top: 35px;
  padding-bottom: 35px;
  padding-left: 10px;
  padding-right: 10px; }

.promo {
  min-height: 860px;
  /*цвет отредактировать*/
  background-image: url("../img/promo-desktop.jpg");
  background-repeat: no-repeat;
  background-position: center 0;
  background-size: auto 860px; }

.promo__wrapper {
  max-width: 1200px;
  min-height: 860px;
  margin: 0 auto;
  display: flex;
  flex-grow: row;
  justify-content: flex-end;
  align-items: center; }

.promo__info {
  align-items: center;
  width: 50%; }

.page-main__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  display: flex; }

.offers {
  display: flex;
  flex-wrap: wrap; }

.offers__info {
  display: flex;
  flex-direction: column;
  width: 100%; }

.offers__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 100%; }

.offers__item {
  position: relative;
  margin: auto; }

.offers__name {
  position: absolute;
  background-color: #ffffff; }
